import { Button, Form } from "react-bootstrap";
import { useState } from "react";
import { Link } from "react-router-dom";
import Dashboard from "./Dashboard";

export default function Login() {
  const [details, setDetails] = useState({ email: "", password: "" });

  const submitHandler = (e) => {
    e.preventDefault();
    LoginStatus(details);
  };

  const [user, setUser] = useState({ name: "", email: "", password: "" });
  const [error, setError] = useState("");

  function LoginStatus(details) {
    const data = {
      name: "admin",
      email: "admin@admin.com",
      password: "admin"
    };

    console.log(details);
    if (details.email === data.email && details.password === data.password) {
      console.log("Logged in");
      setUser({
        name: details.name,
        email: details.email
      });
    } else {
      console.log("Details do not match");
      setError("Details do not match");
    }
  }

  const LogoutStatus = () => {
    console.log("logout");
    setUser({ email: "" });
  };

  return (
    <>
      <div className="container">
        {user.email !== "" ? (
          <div className="welcome">
            <h1>Welcome</h1>
            <button onClick={LogoutStatus}>logout</button> <Dashboard />
          </div>
        ) : (
          <Form
            onSubmit={submitHandler}
            className="col-md-6 shadow mx-auto p-5"
          >
            {error !== "" ? <div className="display-5">{error}</div> : ""}
            <Form.Group className="mb-3" controlId="formBasicEmail">
              <Form.Label>Email address</Form.Label>
              <Form.Control
                type="email"
                placeholder="Enter email"
                onChange={(e) =>
                  setDetails({ ...details, email: e.target.value })
                }
                value={details.email}
              />
            </Form.Group>
            <Form.Group className="mb-3" controlId="formBasicPassword">
              <Form.Label>Password</Form.Label>
              <Form.Control
                type="password"
                placeholder="Password"
                onChange={(e) =>
                  setDetails({ ...details, password: e.target.value })
                }
                value={details.password}
              />
            </Form.Group>
            <Button variant="primary" type="submit">
              Submit
            </Button>{" "}
            <Link className="signup btn btn-dark" to="/signup">
              Signup
            </Link>
          </Form>
        )}
      </div>
    </>
  );
}
